.lightbox {
    position: fixed;
    bottom: 0;
    background-color: $yellow;
    height: 0;
    width: 100vw;
    transition: 0.5s; }

.open {
    height: 75vh;
    transition: 0.5s; }

.underline {
    text-decoration: underline;
    text-underline-offset: 2px; }

.sobre-menu {
    margin: 20px;
    margin-top: 40px;
    height: 65vh;
    overflow-y: scroll;
    width: 88%; }

.sobre-text {
    display: none; }

.show {
    display: block; }

.sobre-img {
    width: 89vw; }

.futuro-img {
    width: 100%;
    height: 35vh;
    object-fit: cover; }


.lightbox-content {
    display: none; }


.lightbox-content.active {
    display: block; }



@media screen and (max-width: 600px) {
    .sobre-text {
        display: block; }

    .sobre-img-double {
        width: 89vw; } }

@media screen and (min-width: 600px) {
    .show {
        display: flex;
        flex-direction: column;
        align-items: center; }

    .sobre-img {
        width: 27vw; }

    .sobre-title {
        padding-left: 20px;
        padding-bottom: 10px;
        width: 20%; }

    .sobre-text {
        position: absolute;
        left: 20%;
        width: 58%;
        top: 35px; }

    .sobre-img-double {
        width: 100%;
        max-height: 47vh;
        object-fit: contain; }

    .double-img {
        display: flex;
        flex-wrap: nowrap !important; }

    .futuro-img {
        height: 42vh;
        margin: 4px; } }
